import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { Button, Form } from 'react-bootstrap';

PointInput.propTypes = {
    
};

function PointInput(props) {

    const {
        timeCome, setTimeCome, 
        timeStop, setTimeStop,
        addPoint, newPoint,
        heightPoint, selectedZone,
        setHeightPoint
    } = props;

    const [error, setError] = useState('');

    const handleOkClick = () => {
        if(timeCome == '' || timeStop == '') {
            setError('bn cha nhp  thng tin');
            return;
        }
        if(selectedZone && (heightPoint < selectedZone.minHeight || heightPoint > selectedZone.maxHeight)){
            setError(' cao ko trong gii hn cho php');
            return;
        }
        if(!newPoint.locationLat){
            setError('Ok ci g vy');
            return;
        }
        addPoint();
        setError('');
    }

    return (
        <>
            <Form.Group controlId="timeCome">
                <Form.Label>Thi gian bay n</Form.Label>
                <Form.Control type="number" value={timeCome} onChange={(e)=>setTimeCome(e.target.value)} placeholder="pht" />
            </Form.Group>

            <Form.Group controlId="timeStop">
                <Form.Label>Thi gian dng</Form.Label>
                <Form.Control type="number" onChange={(e)=>setTimeStop(e.target.value)} value={timeStop} placeholder="pht" />
            </Form.Group>
            <Form.Group controlId="heightPoint">
                <Form.Label>
                     cao {selectedZone != null && `(${selectedZone.minHeight}~${selectedZone.maxHeight})`}
                </Form.Label>
                <Form.Control type="number" onChange={(e)=>setHeightPoint(e.target.value)}  value={heightPoint} placeholder="m" />
            </Form.Group>
            {error !== '' && (<Form.Group controlId="errorMessage">
                <Form.Text className="text-muted">
                    {error}
                </Form.Text>
            </Form.Group>)}
            <Button onClick={handleOkClick}>OK</Button>
        </>
    );
}

export default PointInput;