import React, { useEffect, useState } from 'react';
import { Card, Col, Image, Row, Typography, notification } from 'antd';
import StyleHomeContent from './index.style';
import Meta from 'antd/lib/card/Meta';
import { withRouter } from 'react-router-dom';
import { IMAGES } from '../../constants';
import { useDispatch, useSelector } from "react-redux";
import { actions } from "../../modules/user/store";
import { setHeaders } from "../../services/axios";

const { Title } = Typography;

const HomeContent = ({ history }) => {
  const user = useSelector(state => state.user.user);
  const isLogin = useSelector(state => state.user.isLogin);
  const dispatch = useDispatch();

  const handleClickIncident = (type) => {
    if (!isLogin) {
      history.push('/login');
    } else {
      if (isLogin && (user.type == type || user.role == "SUPER_ADMIN")) {
        dispatch(actions.setProjectType(type));
        history.push('/dashboard');
      } else {
        notification.warning({
          message: "Warning",
          description: "Bn khng c quyn vo d n ny",
        });
      }
    }
  }
  return (
    <StyleHomeContent>
      <Title level={2} className="title">
        H thng gim st, pht hin, cnh bo v x l s s s dng UAV
      </Title>
      <Row gutter={20} className="problems">
        <Col span={6}>
          <Card
            hoverable
            className='card-content'
            onClick={() => handleClickIncident("CHAY_RUNG")}
            cover={
              <Image alt="example" height="300px" src={IMAGES.forestFires} />
            }
          >
            <Meta
              title="S c chy rng"
              description="Gii php gip theo di khu vc rng mt cch thng xuyn v c th d on c s s c xy ra, gip ngn chn v bo v ngun ti nguyn rng."
            />
          </Card>
        </Col>
        <Col span={6}>
          <Card
            hoverable
            className='card-content'
            onClick={() => handleClickIncident("DE_DIEU")}
            cover={
              <Image
                alt='example'
                height='300px'
                src={IMAGES.dike}
              />
            }
          >
            <Meta
              title="S c  iu"
              description="Gim st v m bo cht lng cho h thng  iu mt cch cht ch, hn ch ti a kh nng xy ra s c v a ra nhng cnh bo c th xy ra s c cng nh theo di tnh hnh khi s c  xy ra."
            />
          </Card>
        </Col>
        <Col span={6}>
          <Card
            hoverable
            className='card-content'
            onClick={() => handleClickIncident("LUOI_DIEN")}
            cover={
              <Image
                alt="example"
                height="300px"
                src={IMAGES.highVoltageGrid}
              />
            }
          >
            <Meta
              title="S c li in"
              description="ng dng khoa hc cng ngh tin tin vo vic gim st v pht hin s c li in v vic s dng UAV c xem nh gii php tt v hp l nht hin nay."
            />
          </Card>
        </Col>
        <Col span={6}>
          <Card
            hoverable
            className='card-content'
            onClick={() => handleClickIncident("CAY_TRONG")}
            cover={
              <Image
                alt='example'
                height='300px'
                src={IMAGES.tree}
              />
            }
          >
            <Meta
              title="S c cy trng"
              description="Gim st s dng UAV theo di cy trng c th tit kim mt lng ln thi gian v cng sc nh cc UAV. Ngoi ra s dng UAV  phun thuc tr su s hiu qu hn khi p dng vi trang tri quy m ln, m bo sc khe cho ngi thc hin."
            />
          </Card>
        </Col>
      </Row>
    </StyleHomeContent>
  );
};

export default withRouter(HomeContent);