import React from 'react';
import { Tabs } from 'antd';
import {
  UnorderedListOutlined,
  AppleOutlined,
  AndroidOutlined,
} from '@ant-design/icons';
import './style.css';
import ListIncidents from '../../components/IncidentComponents/ListIncidents';
import ListReport from '../../components/IncidentComponents/ListReport';
import ListSupports from '../../components/IncidentComponents/ListSupports';
import Schedule from '../../components/IncidentComponents/Schedule';
import ListStaff from '../../components/IncidentComponents/ListStaff';
import SettingSchedule from '../../components/IncidentComponents/SettingSchedule';
import TypeTask from '../../components/IncidentComponents/TypeTask';

const { TabPane } = Tabs;

const Incident = () => {
  return (
    <div>
      {' '}
      <Tabs defaultActiveKey="1">
        <TabPane tab={<span> Danh sch cng vic x l </span>} key="1">
          <ListIncidents />
        </TabPane>
        <TabPane tab={<span> Danh sch bo co kt qu </span>} key="2">
          <ListReport />
        </TabPane>
        {/* <TabPane tab={<span>Danh sch yu cu h tr</span>} key="3">
          <ListSupports />
        </TabPane> */}
        {/* <TabPane tab={<span>Lch lm vic</span>} key="4">
          <Schedule />
        </TabPane> */}
        <TabPane tab={<span>Danh sch nhn vin</span>} key="5">
          <ListStaff />
        </TabPane>
        <TabPane tab={<span>Cu hnh lch lm vic</span>} key="6">
          <SettingSchedule />
        </TabPane>
        <TabPane tab={<span>Loi cng vic x l s c</span>} key="7">
          <TypeTask />
        </TabPane>
      </Tabs>
    </div>
  );
};

export default Incident;
