import React, { Fragment } from "react";
import { Col, Input, Row, Button, Select } from "antd";

const { Option } = Select;
const { Search } = Input;

const Filter = ({ filter, setFilter, listPermissions, listRoles }) => {

    const handlResetFilter = () => {
        setFilter({
            page_id: 0,
            page_size: 20,
            search: "",
            role_id: 'Cha xc nh',
            permission_id: 'Cha xc nh',
        });
    };

    const renderSelectRole = () => (
        <Select
            className='select-box'
            value={filter ? filter.role_id : "Cha xc nh"}
            onChange={(value) => setFilter({ ...filter, role_id: value })}
            defaultValue='Cha xc nh'
            style={{ width: "100%" }}>
            <Option key={-1} value='Cha xc nh'>
                Cha xc nh
            </Option>
            {listRoles && listRoles.map((role, index) => {
                return (
                    <Option key={index} value={role.id}>
                        {role.code}
                    </Option>
                );
            })}
        </Select>
    );

    const renderSelectPermission = () => (
        <Select
            className='select-box'
            value={filter ? filter.permission_id : "Cha xc nh"}
            onChange={(value) => setFilter({ ...filter, permission_id: value })}
            defaultValue='Cha xc nh'
            style={{ width: "100%" }}>
            <Option key={-1} value='Cha xc nh'>
                Cha xc nh
            </Option>
            {listPermissions && listPermissions.map((permission, index) => {
                return (
                    <Option key={index} value={permission.id}>
                        {permission.resource}
                    </Option>
                );
            })}
        </Select>
    );

    return (
        <Fragment>
            <Row gutter={[16, 16]}>
                <Col span={12}>Danh sch quyn theo chc nng</Col>
                <Col flex="right" span={2} offset={10}>
                </Col>
            </Row>
            <Row gutter={[16, 16]}>
                <Col span={7} style={{ display: "flex" }}>
                    <label htmlFor="" style={{ width: "20%" }}>
                        Chc v
                    </label>
                    {renderSelectRole()}
                </Col>
                <Col span={7} style={{ display: "flex" }}>
                    <label htmlFor="" style={{ width: "20%" }}>
                        Quyn
                    </label>
                    {renderSelectPermission()}
                </Col>
            </Row>
            <Row gutter={[16, 16]}>
                <Col span={8}></Col>
            </Row>
            <Row>
                <Col span={2} style={{ display: "flex", margin: "0 auto" }}>
                    <Button
                        type="primary"
                        block
                        style={{ marginBottom: 10 }}
                        onClick={handlResetFilter}
                    >
                        Reset
                    </Button>
                </Col>
            </Row>
        </Fragment>
    );
};

export default Filter;
